<template>
    <div>
        <div class="topBar fixed-top extralightBackground rounded">
            <div class="d-flex justify-content-around my-1"> 
                <!-- <h5 style="color:white">Welcome to Capri Cove</h5> -->
                <img src="/img/logos/logo.png" height=38px class="my-0">
            </div>
        </div>

        

        <div class="container my-3">
            <img src="/img/banners/banner.jpg" width="100%" height="150px" class="rounded mt-2">
            <div class="card shadowed mt-4">
                <div class="d-flex justify-content-between mediumBackground rounded p-2">
                    <div>
                        <p class="mb-0" style="font-size:12px;color:#d2dedb">delivery fee</p>
                        <div class="d-flex justify-content-start">
                            <p class="my-0" style="font-size:30px;color:white">฿</p>
                            <p class="my-0" style="font-size:30px;font-weight:900;color:white">{{$parent.deliveryInfo.deliveryFee}}</p>
                        </div>
                    </div>
                    <p style="color:white">{{$parent.deliveryInfo.duration.humanReadable}}</p>

                </div>
                
                <div class="p-2">
                    <div class="d-flex justify-content-start">
                        <i class="fas fa-map-marker-alt my-auto lightColour mr-2 my-auto" style="align-items:baseline"></i>
                        <p class="mb-1 darkColour mr-2 my-auto" style="align-items:baseline"><b>{{$parent.deliveryInfo.distance.humanReadable}}</b></p>
                        <p class="text-secondary my-auto" style="font-size:14px;align-items:baseline;">from Capri Cove</p>
                    </div>

                    <p class="mt-1" style="font-size:12px;color:silver">{{$parent.deliveryInfo.destination}}</p>
                </div>
            </div>

            <div class="mt-4">
                <p class="mb-0 ml-2 darkColour"><b>Address</b></p>
                <div class="d-flex justify-content-between my-1">
                    <vue-google-autocomplete
                        id="map"
                        classname="form-control"
                        v-bind:placeholder="$parent.deliveryInfo.destination"
                        v-on:placechanged="getAddressData"
                        country="th"
                        ref="addressInput"
                    >
                    </vue-google-autocomplete>
                    <a href="#" @click.prevent="requestLocation()" class="my-auto">
                        <i class="fas fa-map-marker-alt my-auto lightColour mx-3 my-auto"></i>
                    </a>
                </div>

                
            </div>
        </div>

        <div class="d-flex justify-content-center my-2">
            <div class="m-2">
                <div class="lightBackground p-2 rounded" v-on:click="setMode('delivery')">
                    <p class="text-center my-auto" style="color:white">Delivery</p>
                </div>
            </div>
            <div class="m-2">
                <div class="lightBackground p-2 rounded" v-on:click="setMode('takeaway')">
                    <p class="text-center my-auto" style="color:white">Takeaway</p>
                </div>
            </div >
            <div class="m-2">
                <div class="lightBackground p-2 rounded" v-on:click="setMode('dinein')">
                    <p class="text-center my-auto" style="color:white">Dine in</p>
                </div>
            </div >
        </div>
        <div id = "footer" class = "fixed-bottom">
            <div class="navBar">
                <div class="row my-auto" id="bottomNav" >
                    <div class="col-3 text-center mt-1">
                        <router-link :to="{ name: 'home'}" style="color:none">
                            <i class="fas fa-home navIcon mediumColour"></i>
                            <p class = "navbarText mb-1 mediumColour">Home</p>
                        </router-link>
                    </div>
                    <div class="col-3 text-center mt-1">
                        <router-link :to="{ name: 'cart'}" style="color:none">
                            <i class="fas fa-shopping-cart navIcon lightColour"></i>
                            <p class = "navbarText mb-1 lightColour">Cart</p>
                        </router-link>
                    </div>
                    <div class="col-3 text-center mt-1">
                        <router-link :to="{ name: 'support'}" style="color:none">
                            <i class="fas fa-question navIcon lightColour"></i>
                            <p class = "navbarText mb-1 lightColour">Support</p>
                        </router-link>
                    </div>
                    <div class="col-3 text-center mt-1">
                        <router-link :to="{ name: 'account'}" style="color:none">
                            <i class="fas fa-user navIcon lightColour"></i>
                            <p class = "navbarText mb-1 lightColour">Account</p>
                        </router-link>
                    </div>
                </div>
            </div>
        </div>
        
    </div>

</template>

<script>
import VueGoogleAutocomplete from 'vue-google-autocomplete'
import NavBar from '../components/NavBar'

export default {
    components: {
        VueGoogleAutocomplete,
        NavBar
    },
    name: "OrderOptions",
    data () {
        return {
            useGps: true,
            addressData: {},
            address: ""
        }
    },
    created(){
        var orderMode = window.localStorage.getItem("orderMode")
        if (this.$route.params.redirect){
            if (this.isValidOrderMode(orderMode)){
                this.setMode(orderMode)
            }
        }
        
    },
    methods: {
        setMode(mode){
            this.$parent.orderMode = mode
            window.localStorage.setItem("orderMode", mode)
            this.$router.push({name: "home", params: {orderMode: mode}})
            
        },
        isValidOrderMode(orderMode){
            if (orderMode == "delivery"){
            return true
            }
            if (orderMode == "takeaway"){
            return true
            }
            if (orderMode == "dinein"){
            return true
            }
            return false
        },
        getAddressData(addressData, placeResultData) {
            this.addressData = addressData;
            this.$parent.latitude = addressData.latitude
            this.$parent.longitude = addressData.longitude
            this.placeResultData = placeResultData
            this.address = placeResultData.formatted_address
            this.$parent.getDeliveryFee(addressData.latitude, addressData.longitude)
            window.console.log(addressData)
            window.console.log(placeResultData)
        },
        requestLocation(){
            this.$refs.addressInput.clear()
            if (navigator.geolocation) {
          navigator.geolocation.getCurrentPosition(this.savePosition , 
            function showError(error) {
            switch(error.code) {
              case error.PERMISSION_DENIED:
                this.$parent.deliveryInfo = {}
                break;
              case error.POSITION_UNAVAILABLE:
                this.$parent.deliveryInfo = {}
                break;
              case error.TIMEOUT:
                this.$parent.deliveryInfo = {}
                break;
              case error.UNKNOWN_ERROR:
                this.$parent.deliveryInfo = {}
                break;
            }
});
        } else {
            window.console.log("Geolocation is not supported by this browser.")
            this.$parent.deliveryInfo = {}
        }
        },
        savePosition(position) {
            this.$parent.latitude = position.coords.latitude
            this.$parent.longitude = position.coords.longitude
            window.console.log(this.$parent.latitude)

            //do other stuff

            this.axios.post(this.$parent.api + 'processor/get_delivery_fee', {
            latitude: this.$parent.latitude,
            longitude: this.$parent.longitude
            })
            .then(res => {
            window.console.log(res.data)
            this.$parent.deliveryInfo = res.data
            window.localStorage.setItem("deliveryInfo", JSON.stringify(this.$parent.deliveryInfo))

            if (this.$parent.deliveryInfo.destination.length > 50){
            this.$parent.deliveryInfo.shortenedAddress = this.$parent.deliveryInfo.destination.substring(0, 50) + "..."
            }
            else (this.$parent.deliveryInfo.shortenedAddress = this.$parent.deliveryInfo.destination)

            this.address = this.$parent.deliveryInfo.destination

            }
            )
            .catch(function (error) {window.console.log(error)});

        }
    }
}
</script>

<style scoped>


    .blueBox {
        background-color: #55d6d0;
    }

    .blueBox p {
        color:white
    }


    .background  {
        background-color: lightgrey;
        background-size: cover;
    }

    .topBar {
        box-shadow: 0px -1px 10px 2px #859096;
        border-color: lightgrey;
        border-style: solid;
        border-width: 1px
    }


</style>