<template>
  <div id="app" class="d-flex flex-column h-100">
    <!-- header area -->
  
    <!-- body area -->
    <!-- <div class="container flex-grow-1 bodyArea"> -->
      <error />
      <div class="mt-5">
        <router-view />
        <div class="pageBottom"></div>
      </div>
    <!-- </div> -->
    <!-- footer area -->
    
  </div>
</template>

<script>
import NavBar from "./components/NavBar";
import Error from "./components/Error";
import SearchBar from "./components/SearchBar"

export default {
  components: {
    NavBar,
    Error,
    SearchBar
  },
  data() {
      return {
        latitude: 0.0,
        longitude: 0.0,
        orderMode: "",
        orders:[],
        productTotal: 0,
        orderTotal: 0,
        deliveryInfo: {
          deliveryFee: 0, 
          destination: "",
          distance: {
            humanReadable: "",
            inMeters: 0
          },
          duration: {
            humanReadable: "",
            inSeconds: 0
          },
          shortenedAddress: "Click here to change your delivery location"
        },
        loggedIn: false,
        username: "",
        password: "",
        accessToken: "",
        refresherToken: "",
        config: {},
        api: 'https://capriapp-backend.herokuapp.com/'
        //https://capriapp-backend.herokuapp.com/order/confirm_order
      }
    },
    created() {
      if(window.localStorage.getItem("orderMode") != null){
        this.orderMode = window.localStorage.getItem("orderMode")
      }
      if(window.localStorage.getItem("orders") != null){
        this.orders = JSON.parse(window.localStorage.getItem("orders"))
        window.console.log("orders: " + this.orders)
      }
      if(window.localStorage.getItem("deliveryInfo") != null){
        this.deliveryInfo = window.localStorage.getItem("deliveryInfo")
      }
      if (navigator.geolocation) {
          navigator.geolocation.getCurrentPosition(this.savePosition , 
            function showError(error) {
            switch(error.code) {
              case error.PERMISSION_DENIED:
                this.deliveryInfo = {}
                break;
              case error.POSITION_UNAVAILABLE:
                this.deliveryInfo = {}
                break;
              case error.TIMEOUT:
                this.deliveryInfo = {}
                break;
              case error.UNKNOWN_ERROR:
                this.deliveryInfo = {}
                break;
            }
});
        } else {
            window.console.log("Geolocation is not supported by this browser.")
            this.deliveryInfo = {}
        }

        this.accessToken = localStorage.getItem("accessToken")
        window.console.log(this.accessToken)
        this.refresherToken = localStorage.getItem("refresherToken")
        window.console.log(this.refresherToken)
        // if(this.token != null){
        //   this.axios.defaults.headers.common['Authorization'] = 'Bearer ' + this.token;
        // }
        // this.axios.defaults.headers.common['Access-Control-Allow-Origin'] = '*'

      this.updateConfig()

      

      
    },

    mounted() {

    },

  methods: {
    savePosition(position) {
      this.latitude = position.coords.latitude
      this.longitude = position.coords.longitude
      this.getDeliveryFee(this.latitude, this.longitude)
      window.console.log(this.latitude)

      
    },
    setCoordinates(lat,long){
      this.latitude = lat
      this.longitude = long
    },
    getDeliveryFee(lat, long){
      this.axios.post(this.api + 'processor/get_delivery_fee', {
        latitude: lat,
        longitude: long
      })
      .then(res => {
      window.console.log(res.data)
      this.deliveryInfo = res.data

      if (this.deliveryInfo.destination.length > 50){
        this.deliveryInfo.shortenedAddress = this.deliveryInfo.destination.substring(0, 50) + "..."
      }
      else (this.deliveryInfo.shortenedAddress = this.deliveryInfo.destination)

      }
      )
      .catch(function (error) {window.console.log(error)});
    },
    acceptOrder(order){
            this.axios.post(this.api + 'order/confirm_order',{
                orderId: order.orderId
            })
            .then(res =>{
                window.console.log(res.data)

               if (res.data == "success"){
                   order.status = 'confirmed'
                   this.$forceUpdate();
               } 
            })
                   
        },

        declineOrder(order){
            this.axios.post(this.api + 'order/decline_order',{
                orderId: order.orderId
            })
            .then(res =>{
               if (res.data == "success"){
                   order.status = 'canceled'
                   this.$forceUpdate();
               } 
            })
        },
        getNewAccessToken(){
          window.console.log(this.refresherToken)
          this.axios.post(this.api + 'authenticate/token', {
              string: this.refresherToken

          })
          .then(res => {
            window.console.log(res.data)
            this.accessToken = res.data.accessToken
            window.localStorage.setItem("accessToken", res.data.accessToken)
          })
          .catch(function (error) {window.console.log("GetNewAccessToken failed")});
        },
        updateConfig(){
          this.config = {
            headers: { 
            'Authorization': "Bearer " + this.accessToken,
            'Access-Control-Allow-Origin': '*'

            }
          }
        }
  
  },
  


};
</script>


