<template>
    <div style="background-color:#f5f5f5">
        <div class="lightBackground fixed-top">
            <div class="locationBar" v-if="deliveryInfo!={}">
                <div class="d-flex justify-content-center text-center p-0">
                    <i class="fas fa-map-marker-alt my-auto lightColour mr-2 my-auto" style="font-size:10px"></i>
                    <p class="my-1 p-0" style="color:grey;font-size:10px"><b>{{deliveryInfo.shortenedAddress}}</b></p>
                </div>
                <div v-if="deliveryInfo=={}">
                    <p style="color:grey;font-size:0.7rem"><b>We can not yet determine your location</b></p>
                </div>

            </div>
            <div class="d-flex justify-content-around my-2">
                <a href="#" @click.prevent="$router.go(-1)" style="color:white">
                    <i class="fas fa-chevron-left my-auto" style="color:white"></i>
                </a>
                <h5 style="color:white">Your cart</h5>
             
                <a href="#" v-on:click="requestLocation()">
                    <i class="fas fa-map-marker-alt my-auto" style="color:white"></i>
                </a>
            </div>
        </div>
        <div class="mt-5">
            <div v-if="items.length < 1">
                <p>You current have no items in your cart</p>
            </div>
            <div v-if="items.length > 0">
                <div class="whiteCard mb-1 pb-1">
                    <div class="mx-4">
                        <div v-for="(item, index) in items" :key="index">
                                <div class="d-flex justify-content-between">
                                    <div class="row">
                                        <img class="py-1 mx-2 my-auto" v-bind:src="'/img/menus/' + items[index].src" style="width:85px;height:85px;object-fit:cover;border-radius: 15%;">
                                        <div class="px-2">
                                            <p class="my-auto" style="font-size:1rem;color:black"><b>{{item.quantity}} {{item.menuName}}</b></p>
                                            <p v-if="item.price == 0" class="my-2 text-secondary" style="font-size:0.8rem" ><b>{{item.optionString}}</b></p>
                                            <p class="my-2 text-muted" style="font-size:0.8rem" v-for="(addOn, index) in item.selectedAddOns" :key="index">{{addOn}}</p>
                                            <p class="my-2 text-muted" style="font-size:0.7rem" >{{item.menuNotes}}</p>
                                            <p v-if="item.promotionType == 'percent'" class="text-muted mb-0" style="font-size:0.8rem">After {{item.promotionAmount}}% discount</p>
                                            <p v-if="item.promotionType == 'amount'" class="text-muted mb-0" style="font-size:0.8rem">After ฿{{item.promotionAmount}} discount</p>
                                        </div>
                                    </div>
                                    <div class="my-auto">
                                        <p v-if="item.promotionType == 'percent'" class="my-auto" style="font-size:15px"><b>{{item.orderPrice * (100 - item.promotionAmount) / 100}}</b></p>
                                        <p v-if="item.promotionType == 'amount'" class="my-auto" style="font-size:15px"><b>{{item.orderPrice - item.promotionAmount}}</b></p>
                                        <p v-if="item.promotionType == null" class="my-auto" style="font-size:15px"><b>{{item.orderPrice}}</b></p>
                                    </div>
                                </div>

                                <div class="d-flex justify-content-end">
                                    <router-link :to="{ name: 'editItem', params: { index: index, id: item.menuId }}">
                                        <label class="mx-2" style="color: #49b3cd;font-size:12px">Edit</label>
                                    </router-link>
                                    <div class="mx-2">
                                       <label style="color: #49b3cd;font-size:12px" v-on:click="$parent.orders.splice(index, 1)">Remove</label> 
                                    </div>
                                </div>
                                
                            <hr class="mt-0" v-if="index < items.length - 1">
                        </div>
                    </div>
                </div>
                <div class="whiteCard mb-1 py-3">
                    <div class="mx-4">
                        <div class="d-flex justify-content-between">
                            <div class="m-2">
                                <div v-if="this.$parent.orderMode == 'delivery'" class="lightBackground p-2 rounded" v-on:click="setMode('delivery')">
                                    <p class="text-center my-auto" style="color:white">Delivery</p>
                                </div>
                                <div v-if="this.$parent.orderMode != 'delivery'" class="p-2 rounded" style="background-color:lightgrey" v-on:click="setMode('delivery')">
                                    <p class="text-center my-auto" style="color:white">Delivery</p>
                                </div>
                            </div>
                            <div class="m-2">
                                <div v-if="this.$parent.orderMode == 'takeaway'" class="lightBackground p-2 rounded" v-on:click="setMode('takeaway')">
                                    <p class="text-center my-auto" style="color:white">Take away</p>
                                </div>
                                <div v-if="this.$parent.orderMode != 'takeaway'" class="p-2 rounded" style="background-color:lightgrey" v-on:click="setMode('takeaway')">
                                    <p class="text-center my-auto" style="color:white">Take away</p>
                                </div>
                            </div>
                            <div class="m-2">
                                <div v-if="this.$parent.orderMode == 'dinein'" class="lightBackground p-2 rounded" v-on:click="setMode('dinein')">
                                    <p class="text-center my-auto" style="color:white">Dine in</p>
                                </div>
                                <div v-if="this.$parent.orderMode != 'dinein'" class="p-2 rounded" style="background-color:lightgrey" v-on:click="setMode('dinein')">
                                    <p class="text-center my-auto" style="color:white">Dine in</p>
                                </div>
                            </div>
                        </div>
                        <div v-if="$parent.orderMode == 'delivery'">
                            <label class="text-muted my-2" style="font-size:12px">{{this.$parent.deliveryInfo.destination}}</label>
                            <div class="d-flex justify-content-end">
                                <router-link :to="{ name: 'editLocation'}">
                                    <label style="color: #49b3cd;font-size:12px">Edit</label>
                                </router-link>
                            </div>
                        </div> 
                    </div>
                </div>
                <div class="whiteCard mb-1 py-3">
                    <div class="mx-4">
                        <div v-if="$parent.orderMode == 'delivery'" class="d-flex justify-content-between">
                            <p>Delivery fee</p>
                            <p>{{deliveryFee}}</p>
                        </div>
                        <div class="d-flex justify-content-between">
                            <p><b>Total</b></p>
                            <p>{{totalPrice + deliveryFee}}</p>
                        </div>
                    </div>
                </div>
            </div>
            
        </div>
        <div id = "footer" class = "fixed-bottom">
            <div class="d-flex justify-content-center my-1">
                <router-link :to="{ name: 'checkout'}" v-if="$parent.orders.length > 0" class="w-100 d-flex justify-content-center">
                    <div class="rounded mediumBackground d-flex justify-content-center my-0" style="width:80%" v-on:click="submitOrder()">
                        <p class="my-2" style="color:white">Checkout</p>
                    </div>
                </router-link>
                <div v-if="$parent.orders.length < 1" class="rounded greyBackground d-flex justify-content-center my-0" style="width:80%" v-on:click="submitOrder()">
                    <p class="my-2" style="color:white">Checkout</p>
                </div>
            </div>
        </div> 
    </div>
    
</template>
<script>
import SearchBar from "../components/SearchBar.vue"
import NavBar from "../components/NavBar.vue"
import LocationBar from "../components/LocationBar.vue"
export default {
    components: {
        NavBar,
        LocationBar,
        SearchBar
    },
    name: "Cart",
    data() {
        return {
            items: [],
            deliveryInfo: {},
            totalPrice: 0,
            deliveryFee: 0
        }
    },
    created() {
        window.console.log(this.$parent.orders.length)
        this.deliveryInfo = this.$parent.deliveryInfo
        this.items = this.$parent.orders
        if (this.$parent.deliveryInfo.deliveryFee){
            this.deliveryFee = this.$parent.deliveryInfo.deliveryFee
        }
       

            for (var i=0;i<this.items.length;i++){
                var item = this.items[i]
                if (item.promotionType == 'percent'){
                    this.totalPrice += (item.orderPrice * (100 - item.promotionAmount) / 100)
                }
                if (item.promotionType == 'amount'){
                    this.totalPrice += (item.orderPrice - item.promotionAmount)
                }
                if (item.promotionType == null){
                    this.totalPrice += item.orderPrice 
                }
                var optionString = ""
                for (var j=0;j<item.menuOptions.length-1;j++){
                    var option = item.menuOptions[j]
                    optionString += option + ", "
                }

                optionString += item.menuOptions[item.menuOptions.length-1]
                item.optionString = optionString
            }
            this.$parent.productTotal = this.totalPrice



    },
    methods: {  
        setMode(mode){
            this.$parent.orderMode = mode
            window.localStorage.setItem("orderMode", mode)
            if (mode == 'delivery'){
                this.deliveryFee = this.$parent.deliveryInfo.deliveryFee
            }
            else{
                this.deliveryFee = 0
            }
        }
    }
}
</script>

<style scoped>


.roundedCard {
  border-radius: 25px;
  /* border: 1px groove lightgrey ; */
  padding: 0px;
  width: 100%;
  height: 5rem;
  background-size: cover; /* or contain depending on what you want */
  background-position: center center;
  background-repeat: no-repeat;
}

.shadowed {
  box-shadow: 3px 4px 5px 4px lightgrey;

}

.menuCard {
  border-radius: 25px;
  height: 12rem;
}


.cardBody {
  padding: 0px;
  margin: 0px;
  padding-left: 0.5rem;
}

.card-title {
  font-size: 1rem !important;
  color: black;
}

.card-subtitle {
  font-size: 0.8rem !important;
}

.card-text {
  font-size: 0.7rem !important;
  color:black
}

.fonted {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.whiteCard {
background-color: white;
}





</style>