<script>

  import NavBar from "../components/NavBar"
  import HomeHeader from "../components/HomeHeader"
  import { BButton, BModal, VBModal } from "bootstrap-vue";


  export default {
    components: {
      HomeHeader,
      NavBar,
      BModal,
      BButton,
      VBModal
    },
    name: "Test",
    data() {
      return {
        windowWidth: window.innerWidth,
        searchKey: "",
        address: "",
        useGps: true,
        mode: "delivery",
        topProductCards: [],
        promotedMenus: [],
        deliveryInfo: {
          deliveryFee: 0, 
          destination: "",
          distance: {
            humanReadable: "",
            inMeters: 0
          },
          duration: {
            humanReadable: "",
            inSeconds: 0
        }
          },
          shortenedAddress: "sample destination"
      }
    },
    created(){
      if(!this.isValidOrderMode(this.$route.params.orderMode)){
        this.$router.push({name: "orderOptions", params: {redirect: true}})
      }
      this.deliveryInfo = this.$parent.deliveryInfo
      
    },
    mounted() {

      this.axios.get(this.$parent.api + 'menu/get_all_menus')
      .then(res => {
      this.topProductCards = res.data
      window.console.log(this.topProductCards)
      }
      )
      .catch(function (error) {window.console.log(error)});

      this.axios.get(this.$parent.api + 'menu/get_promoted_menus')
      .then(res => {
      this.promotedMenus = res.data
      window.console.log(this.promotedMenus)
      }
      )
      .catch(function (error) {window.console.log(error)});

      window.console.log(this.$auth)
    },
    methods: {
      setMode(mode){
        this.mode = mode;
        this.$parent.orderMode = mode
      },
      search(){
        this.$router.push({name: "search", params: {searchKey: this.searchKey}})
      },
      isValidOrderMode(orderMode){
        if (orderMode == "delivery"){
          return true
        }
        if (orderMode == "takeaway"){
          return true
        }
        if (orderMode == "dinein"){
          return true
        }
        return false
      }
    }
    }
  
</script>


