<template>
  <div style="background-color:#f5f5f5">
    <div id="header" class="fixed-top">
      <div class="row lightBackground">
          <div class="col-2 d-flex justify-content-center my-auto">
            <a href="#" @click.prevent="$router.go(-1)" style="color:white">
              <i class="fas fa-chevron-left my-auto" style="color:white"></i>
            </a>
          </div>
          
          <div class="col-8 d-flex justify-content-center my-1">
            <div class="input-group col-md-4">
              <input class="form-control border-right-0 border" type="search" id="example-search-input" placeholder="Search" v-model="searchKey" >
              <span class="input-group-append">
                <button class="btn btn-outline-secondary border-left-0 border" type="button" style="background-color:white" v-on:click="search()">
                  <i class="fa fa-search lightColour" style="background-color:white"></i>
                </button>
              </span>
            </div>
          </div>

          <div class="col-2 d-flex justify-content-center my-auto">
            <div class="pr-5">
              <router-link :to="{ name: 'orderOptions'}">
                <i class="fas fa-map-marker-alt my-auto" style="color:white"></i>
              </router-link>
            </div>
          </div>
        </div>
    </div>
    <img v-bind:src="'/img/menus/' + menu.src" style="width: 100%; border-radius: 0% 0% 1% 1%;">
    <div class="whiteCard my-1 p-2">
      <div class="d-flex justify-content-between mx-2">
        <p class="darkColour mt-0 mb-0" style="font-size: 20px;"><b>{{menu.name}}</b></p>
        <p class="mediumColour mt-0 mb-0 mx-0" style="font-size: 24px;">฿<b>{{this.displayPrice}}</b></p>
      </div>
      <div class="mx-2">
        <p class="text-muted mt-2 mb-0" style="font-size:14px">{{menu.thaiDescription}}</p>
        <p class="text-muted mt-1" style="font-size:12px">{{menu.englishDescription}}</p>
      </div>
    </div>

    <!-- //Options area -->
    <div class="p-3 whiteCard my-1" v-if="optionCategory1!=null">
      <div class="form-group  my-0">
        <p class="optionTitle"><b>{{optionCategory1.optionCategoryName}}</b></p>
        <div v-for="(option, index) in optionCategory1.options.slice(0,optionCategory1.options.length-1)" :key="index" v-on:click="option1Selected=option.optionName;updateDisplayPrice()">
          <div class="form-check my-0" >
            <div class="d-flex justify-content-between">
              <div class="radioContainer">
                <label class="form-check-label" for="exampleRadios1">
                  <input class="form-check-input" type="radio" name="optionCategory1" v-on:change="updateDisplayPrice()" :value="option.optionName" v-model="option1Selected">
                  <span class="checkmark my-auto"></span>
                  <p v-if="option.optionName==option1Selected" class="mx-3 my-0 text-muted"><b>{{option.optionName}}</b></p>
                  <p v-if="option.optionName!=option1Selected" class="mx-3 my-0 text-muted">{{option.optionName}}</p>
                </label>
              </div>
              <div>
                <p v-if="option.optionName==option1Selected" class="my-0 text-muted"><b>{{option.optionPrice}}</b></p>
                <p v-if="option.optionName!=option1Selected" class="my-0 text-muted">{{option.optionPrice}}</p>
              </div>
            </div>
          </div>
          <hr>          
        </div>
        <div v-on:click="option1Selected=optionCategory1.options[optionCategory1.options.length-1].optionName;updateDisplayPrice()">
          <div class="form-check my-0" >
            <div class="d-flex justify-content-between">
              <div class="radioContainer">
                <label class="form-check-label" for="exampleRadios1"> 
                  <input class="form-check-input" type="radio" name="optionCategory1" v-on:change="updateDisplayPrice()" :value="optionCategory1.options[optionCategory1.options.length-1].optionName" v-model="option1Selected">
                  <span class="checkmark my-auto"></span>
                  <p v-if="optionCategory1.options[optionCategory1.options.length-1].optionName==option1Selected" class="mx-3 my-0 text-muted"><b>{{optionCategory1.options[optionCategory1.options.length-1].optionName}}</b></p>
                  <p v-if="optionCategory1.options[optionCategory1.options.length-1].optionName!=option1Selected" class="mx-3 my-0 text-muted">{{optionCategory1.options[optionCategory1.options.length-1].optionName}}</p>
     
                </label>
              </div>
              <div>
                <p v-if="optionCategory1.options[optionCategory1.options.length-1].optionName==option1Selected" class="my-0 text-muted"><b>{{optionCategory1.options[optionCategory1.options.length-1].optionPrice}}</b></p>
                <p v-if="optionCategory1.options[optionCategory1.options.length-1].optionName!=option1Selected" class="my-0 text-muted">{{optionCategory1.options[optionCategory1.options.length-1].optionPrice}}</p>              
              </div>
            </div>
          </div>        
        </div>
      </div>
    </div>

    <div class="p-3 whiteCard my-1" v-if="optionCategory2!=null">
      <div class="form-group  my-0">
        <p class="optionTitle"><b>{{optionCategory2.optionCategoryName}}</b></p>
        <div v-for="(option, index) in optionCategory2.options.slice(0,optionCategory2.options.length-1)" :key="index" v-on:click="option2Selected=option.optionName;updateDisplayPrice()">
          <div class="form-check my-0" >
            <div class="d-flex justify-content-between">
              <div class="radioContainer">
                <label class="form-check-label" for="exampleRadios1">
                  <input class="form-check-input" type="radio" name="optionCategory2" v-on:change="updateDisplayPrice()" :value="option.optionName" v-model="option2Selected">
                  <span class="checkmark my-auto"></span>
                  <p v-if="option.optionName==option2Selected" class="mx-3 my-0 text-muted"><b>{{option.optionName}}</b></p>
                  <p v-if="option.optionName!=option2Selected" class="mx-3 my-0 text-muted">{{option.optionName}}</p>
                </label>
              </div>
              <div>
                <p v-if="option.optionName==option2Selected" class="my-0 text-muted"><b>{{option.optionPrice}}</b></p>
                <p v-if="option.optionName!=option2Selected" class="my-0 text-muted">{{option.optionPrice}}</p>
              </div>
            </div>
          </div>
          <hr>          
        </div>
        <div v-on:click="option2Selected=optionCategory2.options[optionCategory2.options.length-1].optionName;updateDisplayPrice()">
          <div class="form-check my-0" >
            <div class="d-flex justify-content-between">
              <div class="radioContainer">
                <label class="form-check-label" for="exampleRadios1"> 
                  <input class="form-check-input" type="radio" name="optionCategory2" v-on:change="updateDisplayPrice()" :value="optionCategory2.options[optionCategory2.options.length-1].optionName" v-model="option2Selected">
                  <span class="checkmark my-auto"></span>
                  <p v-if="optionCategory2.options[optionCategory2.options.length-1].optionName==option2Selected" class="mx-3 my-0 text-muted"><b>{{optionCategory2.options[optionCategory2.options.length-1].optionName}}</b></p>
                  <p v-if="optionCategory2.options[optionCategory2.options.length-1].optionName!=option2Selected" class="mx-3 my-0 text-muted">{{optionCategory2.options[optionCategory2.options.length-1].optionName}}</p>
     
                </label>
              </div>
              <div>
                <p v-if="optionCategory2.options[optionCategory2.options.length-1].optionName==option2Selected" class="my-0 text-muted"><b>{{optionCategory2.options[optionCategory2.options.length-1].optionPrice}}</b></p>
                <p v-if="optionCategory2.options[optionCategory2.options.length-1].optionName!=option2Selected" class="my-0 text-muted">{{optionCategory2.options[optionCategory2.options.length-1].optionPrice}}</p>              
              </div>
            </div>
          </div>        
        </div>
      </div>
    </div>

    <!-- Addons area -->
    <!-- do on click to trigger value change instead of v-model -->
    <div class="p-3 whiteCard my-1">
      <p class="optionTitle"><b>Add ons</b></p>
      <div class="form-check mx-0 px-0" v-for="(option, index) in menu.addOns" :key="index" v-on:click="clickAddOn(option.optionName);updateDisplayPrice()">
        <div class="mx-0 px-0 d-flex justify-content-between">
          <div class="checkboxContainer">
              <input v-model="selectedAddOns" :key="index" v-on:change="updateDisplayPrice()" class="form-check-input" type="checkbox" v-bind:value="option.optionName" v-bind:id="option.optionName">
              <span class="checkboxCheckmark my-auto"></span>
              <p v-if="selectedAddOns.includes(option.optionName)" class="mx-1 my-0 text-muted"><b>{{option.optionName}}</b></p>
              <p v-if="!selectedAddOns.includes(option.optionName)" class="mx-1 my-0 text-muted">{{option.optionName}}</p>
          </div>
          <div>
            <p v-if="selectedAddOns.includes(option.optionName)" class="mx-1 my-0 text-muted"><b>{{option.optionPrice}}</b></p>
            <p v-if="!selectedAddOns.includes(option.optionName)" class="mx-1 my-0 text-muted">{{option.optionPrice}}</p>           
          </div>
        </div>
        <hr>
      </div>
    </div>


    <!-- //Notes area -->
    <div class="whiteCard my-1 p-2">
      <div class="mx-2">
        <p class="optionTitle"><b>Notes</b></p>
        <div class="form-group">
          <input name="notes"  class="form-control" aria-describedby="smth" placeholder="No gluten?" v-model="notes">
          <div class="row mx-2">
            <small class="form-text text-muted">You may specify additional dietary requirements here</small>
          </div>
        </div>
      </div>
    </div>

    <!-- quantity -->
    <div class="whiteCard my-1" >
      <div class="d-flex justify-content-center">
        <div class="card my-2" v-on:click="decrementQuantity()">
          <i class="fas fa-minus mx-4 my-2 lightColour"></i>
        </div>
        <div class="my-2">
          <p class="mx-4 my-2"><b>{{quantity}}</b></p>
        </div>
        <div class="card my-2" v-on:click="incrementQuantity()">
          <i class="fas fa-plus mx-4 my-2 lightColour"></i>
        </div>
      </div>
    </div>      


    <div id = "footer" class = "fixed-bottom w-100">
      <div class="d-flex justify-content-center my-1">
        <div v-if="!addedToCart" class="rounded mediumBackground d-flex justify-content-center my-0" style="width:80%" v-on:click="addToCart()">
          <p class="my-2" style="color:white">Update - {{finalPrice}}</p>
        </div>
        <div v-if="addedToCart" class="rounded d-flex justify-content-center my-0" style="width:80%;background-color:lightgrey">
          <p class="my-2" style="color:white">Updated</p>
        </div>
      </div>
    </div> 
    
  </div> 
</template>


<script>
  import SearchBar from "../components/SearchBar"
  export default {
    components: {
      SearchBar
    },
    name: "EditItem",
    data() {
      return {
          index: null,
          menuId: 0,
          menu: {},
          selected: "",
          options: {},
          selectedOptionName: "",
          selectedAddOns: [],
          selectedOptionCategory: "",
          optionCategory1: null,
          optionCategory2: null,
          optionCategory3: null,
          optionCategory4: null,  
          optionCategory5: null,
          option1Selected: "",
          option2Selected: "",
          option3Selected: "",
          option4Selected: "",
          option5Selected: "",
          quantity: 1,
          invalidSubmission: false,
          displayPrice: 0,
          finalPrice: 0,
          addedToCart: false,
          notes: ""
      }
    },
    created() {
        this.index = this.$route.params.index;
        this.menuId = this.$route.params.id
        window.console.log("id: " + this.menuId)
        var item = this.$parent.orders[this.index]
        this.selectedAddOns = item.selectedAddOns
        this.quantity = item.quantity
        this.notes = item.menuNotes
        this.axios.post(this.$parent.api + 'menu/get_menu_item', {
          id: this.menuId
        })
        .then(res => {
            this.menu = res.data
            window.console.log(this.menu)
            this.displayPrice = this.menu.displayPrice
            this.optionCategory1 = this.menu.optionCategories.length > 0 ? this.menu.optionCategories[0]: null
            this.optionCategory2 = this.menu.optionCategories.length > 1 ? this.menu.optionCategories[1]: null
            this.optionCategory3 = this.menu.optionCategories.length > 2 ? this.menu.optionCategories[2]: null
            this.optionCategory4 = this.menu.optionCategories.length > 3 ? this.menu.optionCategories[3]: null
            this.optionCategory5 = this.menu.optionCategories.length > 4 ? this.menu.optionCategories[4]: null
            this.option1Selected = this.optionCategory1 != null ? item.menuOptions[0]: ""
            this.option2Selected = this.optionCategory2 != null ? item.menuOptions[1]: ""
            this.option3Selected = this.optionCategory3 != null ? item.menuOptions[2]: ""
            this.option4Selected = this.optionCategory4 != null ? item.menuOptions[3]: ""
            this.option5Selected = this.optionCategory5 != null ? item.menuOptions[4]: ""
            if (this.menu.price != 0){
              this.displayPrice = this.menu.price
              if (this.menu.promotionType == 'percent'){
                this.finalPrice = this.displayPrice * (100 - this.menu.promotionAmount) / 100
              }
              if (this.menu.promotionType == 'amount'){
                this.finalPrice = this.displayPrice - this.menu.promotionAmount
              }
            }
            else {
              this.updateDisplayPrice()
            }


        })
        .catch(function (error) {window.console.log(error)});

        


    },
    mounted() {
        



    },
    
    methods: {
      clickAddOn(optionName){
        var found = false
          for(var i=0;i<this.selectedAddOns.length;i++){
            if (this.selectedAddOns[i] == optionName){
              this.selectedAddOns.pop(i)
              found = true
            }
          }

          if (!found){
            this.selectedAddOns.push(optionName)
          }

      },
      addToCart() {
        var selectedOptions = []

        if (this.optionCategory1 != null){
          selectedOptions.push(this.option1Selected)
        }

        if (this.optionCategory2 != null){
          selectedOptions.push(this.option2Selected)
        }

        if (this.optionCategory3 != null){
          selectedOptions.push(this.option3Selected)
        }

        if (this.optionCategory4 != null){
          selectedOptions.push(this.option4Selected)
        }

        if (this.optionCategory5 != null){
          selectedOptions.push(this.option5Selected)
        }

        //check if all options are checked

        window.console.log("displayPrice: " + this.displayPrice)


        var item = {
          menuId: this.menuId,
          menuName: this.menu.name,
          menuOptions: selectedOptions,
          selectedAddOns: this.selectedAddOns,
          orderPrice: this.getPrice() * this.quantity,
          finalPrice: this.finalPrice,
          quantity: this.quantity,
          menuNotes: this.notes,
          src: this.menu.src,
          promotionType: this.menu.promotionType,
          promotionAmount: this.menu.promotionAmount
        }

        window.console.log(item)

        this.$parent.orders[this.index] = item
        window.localStorage.setItem("orders", JSON.stringify(this.$parent.orders))

        this.addedToCart=true

         setTimeout(function(router) {
            router.go(-1)

          }, 500,this.$router);


        // this.$router.push({ name: 'cart'})


        


      },

      back(router){
        router.go(-1)
      },

      updateDisplayPrice(){
        if(this.menu.price == 0){
          this.displayPrice = this.getPrice() * this.quantity
        }
        window.console.log("displayPrice: " + this.displayPrice)
        this.finalPrice = this.displayPrice
        if (this.menu.promotionType == 'percent'){
          this.finalPrice = this.finalPrice * (100 - this.menu.promotionAmount) / 100
        }
        if (this.menu.promotionType == 'amount'){
          this.finalPrice = this.finalPrice - this.menu.promotionAmount
        }
        if (this.addedToCart == true){
          this.addedToCart = false
        }        
      },
      incrementQuantity(){
        this.quantity++
        this.updateDisplayPrice()

      },
      decrementQuantity(){
        if (this.quantity > 1){
          this.quantity--
        }
        this.updateDisplayPrice()
      },
      verifySelected(){
        var allSelected = true

        if (this.optionCategory1 != null && this.option1Selected == ""){
          allSelected = false
        }

        if (this.optionCategory2 != null && this.option2Selected == ""){
          allSelected = false
        }

        if (this.optionCategory3 != null && this.option3Selected == ""){
          allSelected = false
        }

        if (this.optionCategory4 != null && this.option4Selected == ""){
          allSelected = false
        }

        if (this.optionCategory5 != null && this.option5Selected == ""){
          allSelected = false
        }

        if (this.quantity == 0){
          allSelected = false
        }
        return allSelected
    },
      getPrice(){
                
        var price = 0
      

        if (this.menu.price != 0){
          price = this.menu.price
        }
        else{
          if (this.optionCategory1 != null){
            for (var i = 0; i < this.optionCategory1.options.length; i++){
              if (this.option1Selected == this.optionCategory1.options[i].optionName){
                price += this.optionCategory1.options[i].optionPrice
                window.console.log("adding price")
              }
            }
          }
        
          if (this.optionCategory2 != null){
            for (i = 0; i < this.optionCategory2.options.length; i++){
              if (this.option2Selected == this.optionCategory2.options[i].optionName){
                price += this.optionCategory2.options[i].optionPrice
              }
            }
          }

          if (this.optionCategory3 != null){
            for (i = 0; i < this.optionCategory3.options.length; i++){
              if (this.option3Selected == this.optionCategory3.options[i].optionName){
                price += this.optionCategory3.options[i].optionPrice
              }
            }
          }

          if (this.optionCategory4 != null){
            for (i = 0; i < this.optionCategory4.options.length; i++){
              if (this.option4Selected == this.optionCategory4.options[i].optionName){
                price += this.optionCategory4.options[i].optionPrice
              }
            }
          }

          if (this.optionCategory5 != null){
            for (i = 0; i < this.optionCategory5.options.length; i++){
              if (this.option5Selected == this.optionCategory5.options[i].optionName){
                price += this.optionCategory5.options[i].optionPrice
              }
            }
          }
        }

        // loop through selectedAddOns
        for (i=0;i<this.selectedAddOns.length;i++){
          var selectedAddOn = this.selectedAddOns[i]
          for(var j=0;j<this.menu.addOns.length;j++){
            var option = this.menu.addOns[j]
            if (selectedAddOn == option.optionName){
              price += option.optionPrice
            }
          }
        }

        
        

        return price
      }
  }
}
</script>

<style scoped>


  label {
    /* whatever other styling you have applied */
    width: 100%;
    display: inline-block;
  }
  .shadowed {
    box-shadow: 3px 4px 5px 4px lightgrey;

  }

  .whiteCard {
    background-color: white;
  }

  .optionTitle {
    font-size: 1.3rem;
  }

 

</style>